import { getField, updateField } from "vuex-map-fields";

const initialData = {
  id: null,
  nik: null,
  name: null,
  phoneNo: null,
  email: null,
  domicileAddress: null,
  npwpNo: null,
  ktpNo: null,
  birthPlace: null,
  ktpAddress: null,
  birthdayDate: null,
  gender: null,
  martial: null,
  religion: null,
  employeeStatus: null,
  isPartner: false,
  assessment: null,
  initial: null
};
export default {
  namespaced: true,
  state: {
    isOpen: false,
    isNew: false,
    ...initialData
  },
  mutations: {
    updateField,
    close(state) {
      state.isOpen = false;
      this.reset;
    },
    reset(state) {
      state = { ...initialData };
    }
  },
  actions: {
    save: ({ state, commit }) => {
      commit("close");
    },
    add: ({ state, commit }) => {
      commit("reset");
      state.isOpen = true;
      state.isNew = true;
    },
    edit: ({ state, commit }, { payload }) => {
      state.isOpen = true;
      state.isNew = false;
    }
  },
  getters: {
    getField
  }
};