import axios from "axios";
import swals from "sweetalert2";
import { Header } from "./header";
//import { actionLogout } from '../../routes/Login/action/Login';
import Router from "../router";

axios.defaults.baseURL = "https://api-amasys.experd.com/api";

const APIGuard = function(res) {
  if (res.status == 200) {
    return true;
  }
  if (res.status == 401 || !res.auth) {
    Router.push("/logout");
    return false;
  }
  return false;
};

const APIErrorHandler = function(err) {
  if (err.response.status == 401) {
    swals
      .fire({
        title: "Info",
        text: err.response.statusText,
        type: "info",
        allowOutsideClick: false,
        confirmButtonText: "Ya"
      })
      .then(function(confirm) {
        
      });
  }
  if (!err.response.data.auth) Router.push("/logout");
};

export function POST(url, body) {
  return axios
    .post(url, body, Header())
    .then(res => {
      APIGuard(res);
      return res.data;
    })
    .catch(err => {
      APIErrorHandler(err);
      return err;
    });
}

export function GET(url) {
  return axios
    .get(url, Header())
    .then(res => {
      APIGuard(res);
      return res.data;
    })
    .catch(err => {
      APIErrorHandler(err);
      return err;
    });
}

