export default {
  namespaced: true,
  state: {
    drawer: true,
    currRoute: {}
  },
  mutations: {
    toggleDrawer(state) {
      state.drawer = !state.drawer;
    },
    setDrawer(state, drawer) {
      state.drawer = drawer;
    }
  },
  actions: {
    toggleDrawer: ({ commit }) => {
      commit("toggleDrawer");
    },
    setCurrRoute({ commit, state }, currentRoute) {
      state.currRoute = currentRoute;
    }
  },
  getters: {
    drawer: state => {
      return state.drawer;
    },
    currRoute: state => {
      return state.currRoute;
    }
  }
};
