import { mapActions, mapState, mapGetters } from "vuex";
import path from "../../router/path";
import router from "../../router";

export default {
  name: "side-bar",
  components: {},
  props: [],
  data() {
    return {
      mini: false,
      menus: path.reduce((res, val) => {
        if (val.meta)
          if (val.meta.menu)
            res.push({
              title: val.meta.menu.title,
              icon: val.meta.menu.icon,
              path: val.path
            });
        return res;
      }, []),
      searchText: ""
    };
  },
  computed: {
    drawer: {
      get() {
        return this.$store.getters["ui/drawer"];
      },
      set(val) {
        this.$store.commit("ui/setDrawer", val);
      }
    },
    getMenus: function() {
      if (!this.searchText) return this.menus;
      else
        return this.menus.reduce((res, val) => {
          if (val.title.toLowerCase().includes(this.searchText.toLowerCase()))
            res.push(val);
          return res;
        }, []);
    }
  },
  mounted() {},
  methods: {
    routeToMenu: function() {
      if (this.searchText != "" && this.getMenus.length > 0){
        router.push(this.getMenus[0].path);
        this.searchText = '';
      }
    },
    ...mapActions("ui", ["toggleDrawer"])
  }
};
