import Router from "../../router";
import { POST, GET, PUT, DELETE } from '../../api/index'
import { Header } from '../../api/header'

export default {
  namespaced: true,
  state: {
    token: undefined,
    username: "",
    password: ""
  },
  mutations: {
    isAuthenticate(state, dataToken) {
      state.token = dataToken;
    },
    isNotAuthenticate(state){
      localStorage.removeItem('token')
      state.token = ''
    }
  },
  actions: {
    test: ({ commit, state }) => {
      alert("test");
    },
    authenticate: (context, { username, password }) => {
      return new Promise(resolve => {
        let bodyReq = {
          username: username,
          password: password,
          appcode: "S001"
        }
        POST('account/login', bodyReq, Header()).then((res) => {
          localStorage.setItem('token', res.token);
          resolve(res);
          Router.push('/')
        }).catch(e => {
          reject(e)
        })
      })
    },
    logout: (context, payload) => {
      context.commit('isNotAuthenticate')
    }
  },
  getters: {
    isAuthenticated: state => null
  }
};
